<template>
  <div class="home">
    <div class="ci">
      <div class="ci-title">
        <div class="ci-rhythmic">{{ this.ci.rhythmic }}</div>
        <div class="ci-author">{{ this.ci.author }}</div>
      </div>
      <div class="ci-content">
        <div class="ci-paragraphs">
          <div v-for="(item, ix) in this.ci.paragraphs" :key="ix">
            {{ item }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import ci_data from "../../public/json/ci.song.1000.json"

export default {
  name: "HomeView",
  components: {
  },
  data() {
    return {
      ci: {
        author: null,
        paragraphs: null,
        rhythmic: null,
      },
    };
  },
  computed: {
  },
  mounted: function () {
    this.getCi();
  },
  methods: {
    getCi: function () {
      let ix = Math.round(Math.random() * ci_data.length);
      console.log(ci_data[ix]);
      this.ci.author = ci_data[ix].author;
      this.ci.rhythmic = ci_data[ix].rhythmic;
      let paragraphs_temp = [];
      for (let item of ci_data[ix].paragraphs) {
        let i = item.replace(/。/g, '');
        paragraphs_temp.push(i);

      }
      this.ci.paragraphs = paragraphs_temp;
    }
  }
};
</script>

<style lang="scss" scoped>
.home {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
}

@keyframes change-opacity {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0.3;
  }
}

.ci {
  writing-mode: vertical-rl;
  font-family: gufeng;
  font-size: 24px;
  letter-spacing: 8px;

  font-display: block;

  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  margin: 24px 0 0 0;

  gap: 24px;

}


.ci-title {
  display: flex;
  gap: 16px;
}

@media (min-width: 768px) {
  .home {
    height: 100%;
  }

  .ci {
    margin: -48px 0 0 0;
  }
  
}
</style>
